.content.search__log > .page__title__wrap {
  grid-column: span 1 / span 1;
}

.section__grid__wrap.search__log {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.content.search__log section.device__log__search > .section__inner .search__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.content.search__log section.device__log__search > .section__inner .search__wrap p {
  white-space: nowrap;
  margin-right: 12px;
  color: #878787;
}

.content.search__log section.device__log__search > .section__inner .search__wrap > .calendar__wrap {
  display: flex;
  align-items: center;
}

.content.search__log
  section.device__log__search
  > .section__inner
  .search__wrap
  > .calendar__wrap
  > span {
  color: rgb(187, 187, 187);
  margin: 0 8px;
}

.content.search__log section.device__log__search .section__inner > .bottom {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 18px;
  flex-wrap: wrap;
}

.content.search__log section.device__log__search .section__inner > .bottom > button {
  margin: 0 12px;
}

.content.search__log section.device__log__search .section__inner > .bottom > p {
  color: #878787;
}

.content.search__log
  section.device__log__search
  .section__inner
  > .bottom
  > .react-datepicker-wrapper {
  width: auto;
}

@media (max-width: 650px) {
  .content.search__log section.device__log__search > .section__inner .search__wrap {
    flex-direction: column;
    align-items: flex-start;
  }

  .content.search__log
    section.device__log__search
    > .section__inner
    .search__wrap
    > .calendar__wrap {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  .content.search__log
    section.device__log__search
    > .section__inner
    .search__wrap
    > .calendar__wrap
    span {
    display: none;
  }

  .content.search__log .search__wrap .react-datepicker-wrapper {
    margin-bottom: 12px;
  }

  .content.search__log
    section.device__log__search
    > .section__inner
    .search__wrap
    > .calendar__wrap
    input {
    max-width: none;
  }

  .content.search__log section.device__log__search > .section__inner .search__wrap p {
    margin: 0 0 12px;
  }
}
